  transform: none;
  opacity: 0;
  width: 100vw;
  height: 0;
  transition: opacity 0.25s ease-in-out;
}

.popup-active {
  height: 100vh;
  overflow: hidden;
}

.popup-active .off-canvas-menu {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: #000;
  opacity: 0.9;
  transition: opacity 0.25s ease-in-out;
  overflow: auto;
  transform: none;
}

